@use 'base/variables' as v;
@use 'base/mixins' as m;

.listado-ponentes {
    @include m.reset-lista;
    @include m.grid(1, 1rem);

    &__ponente {
        color: v.$azul;
        border: 1px solid v.$azul;
        padding: 1rem;
        border-radius: 1rem;
        text-align: center;
        transition: all 0.1s ease-in-out;
        cursor: pointer;

        &:hover {
            background-color: v.$azul;
            color: v.$blanco;
        }
    }
}